import { useState, useEffect } from "react";
import { useRouter } from "next/router";
import axios from "axios";

export default function ChatDetail() {
  const router = useRouter();
  const { id } = router.query;
  const [chat, setChat] = useState(null);
  const [message, setMessage] = useState("");

  useEffect(() => {
    if (id) {
      fetchChatDetail();
    }
  }, [id]);

  const fetchChatDetail = async () => {
    try {
      const token = localStorage.getItem("token");
      const response = await axios.get(`http://localhost:3000/api/history/chat/${id}`, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });
      setChat(response.data);
    } catch (error) {
      console.error("Gagal mengambil detail chat:", error);
    }
  };

  const sendMessage = async () => {
    try {
      const token = localStorage.getItem("token");
      await axios.put(
        `http://localhost:3000/api/history/chat/${id}`,
        { message: { role: "user", content: message } },
        {
          headers: {
            Authorization: `Bearer ${token}`,
            "Content-Type": "application/json",
          },
        }
      );
      setMessage("");
      fetchChatDetail(); // Refresh chat setelah mengirim pesan
    } catch (error) {
      console.error("Gagal mengirim pesan:", error);
    }
  };

  return (
    <div>
      <button onClick={() => router.push("/chat")}>Kembali</button>
      {chat ? (
        <>
          <h1>{chat.title}</h1>
          <ul>
            {chat.messages.map((msg, index) => (
              <li key={index}>
                <strong>{msg.role}:</strong> {msg.content}
              </li>
            ))}
          </ul>
          <input
            type="text"
            placeholder="Ketik pesan..."
            value={message}
            onChange={(e) => setMessage(e.target.value)}
          />
          <button onClick={sendMessage}>Kirim</button>
        </>
      ) : (
        <p>Loading...</p>
      )}
    </div>
  );
}
